:root {
  --warna-biru: hsl(213, 76%, 46%);
  --warna-cream: hsl(28, 76%, 97%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

h1,
h2,
h3 {
  margin: 0;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow: hidden;
  position: fixed;
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}